import { Component, OnInit } from '@angular/core';
declare var jQuery:any;
declare var $ :any;

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.css']
})
export class AboutComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    jQuery(document).ready(function($){
                    
      $('.accordion-title').click(function(){
        if( !$(this).hasClass('active') ) {
          $('.accordion-title').removeClass('active').find('span').addClass('icon-plus-sign').removeClass('icon-minus-sign');
          $('.accordion-item').slideUp();
    
          $(this).toggleClass('active')
               .find('span').removeClass('icon-plus-sign').addClass('icon-minus-sign').parent()
               .next().slideToggle();
        }
      }).filter(':first').click();
      
    });

    jQuery(document).ready(function($){
      $('.testimonial-widget ul').cycle({
          fx:'scrollLeft',

          timeout:8000,
          speed:300, 
          pager:'.p-testimonial',
          fit: 1,
          width: '100%'
      });
    });


  }

}



