import { Component, OnInit } from '@angular/core';
declare var google:any;

@Component({
  selector: 'app-contacts',
  templateUrl: './contacts.component.html',
  styleUrls: ['./contacts.component.css']
})
export class ContactsComponent implements OnInit {
  //Model goes here 
  //call the global object
  constructor() { }

  ngOnInit() {
    //for google map Api

    var qatarProp = {ID:document.getElementById("googleMapQatar"), 
    coordinateX:25.259797, coordinateY:51.489030, 
    address: "23446 Salwa Road, Doha, Qatar",
    phone: "+974 4469 6226",
    fax: "+974 4469 6987",
    email: "info-qa@alsahoogroup.com" };

    var uaeID = document.getElementById("googleMapUAE")
    var kuID = document.getElementById("googleMapKuwait")

    var myCenter = new google.maps.LatLng(qatarProp.coordinateX,
      qatarProp.coordinateY);

    function initialize()
      {
        var mapProp = {
          center:myCenter,
          zoom:15,
          mapTypeId:google.maps.MapTypeId.ROADMAP
          };

        var map=new google.maps.Map(qatarProp.ID,mapProp);

        var marker=new google.maps.Marker({
          position:myCenter,
          });

        marker.setMap(map);
        }

        google.maps.event.addDomListener(window, 'load', initialize);

  }
  //controller goes here

}

