import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import {APP_BASE_HREF} from '@angular/common'; //for local provider
import { AgmCoreModule } from '@agm/core';
import { FormsModule } from '@angular/forms';

import { HttpModule } from '@angular/http';
import { DataService } from "./data.service";

import { AppComponent } from './app.component';
import { ContactsComponent } from './components/contacts/contacts.component';
import { HomeComponent } from './components/home/home.component';
import { BrandsComponent } from './components/brands/brands.component';
import { DownloadsComponent } from './components/downloads/downloads.component';
import { CorporateComponent } from './components/corporate/corporate.component';
import { NewseventsComponent } from './components/newsevents/newsevents.component';
import { JobsComponent } from './components/jobs/jobs.component';
import { QatarComponent } from './components/contacts/qatar/qatar.component';
import { UaeComponent } from './components/contacts/uae/uae.component';
import { KuwaitComponent } from './components/contacts/kuwait/kuwait.component';
import { SaudiComponent } from './components/contacts/saudi/saudi.component';
import { AquathermComponent } from './components/brands/aquatherm/aquatherm.component';
import { ValsirComponent } from './components/brands/valsir/valsir.component';
import { BravatComponent } from './components/brands/bravat/bravat.component';
import { AquasanComponent } from './components/brands/aquasan/aquasan.component';
import { LoginComponent } from './components/login/login.component';
import { AboutComponent } from './components/corporate/about/about.component';
import { VideosComponent } from './components/corporate/videos/videos.component';
import { ProjectsComponent } from './components/corporate/projects/projects.component';
import { GalleryComponent } from './components/corporate/gallery/gallery.component';
import { AzulevComponent } from './components/brands/azulev/azulev.component';

//declare routes here
const appRoutes: Routes = [
  {path:'', component:HomeComponent},
  {path:'brands', component:BrandsComponent},
  {path:'corporate', component:CorporateComponent},
  {path:'downloads', component:DownloadsComponent},
  {path:'newsevents', component:NewseventsComponent},
  {path:'contacts', component:ContactsComponent},
  {path:'jobs',component:JobsComponent},
  {path:'qatar',component:QatarComponent},
  {path:'uae',component:UaeComponent},
  {path:'login',component:LoginComponent},
  {path:'about',component:AboutComponent},
  {path:'videos',component:VideosComponent},
  {path:'projects',component:ProjectsComponent},
  {path:'gallery',component:GalleryComponent},
  {path:'aquatherm',component:AquathermComponent},
  {path:'valsir',component:ValsirComponent},
  {path:'bravat',component:BravatComponent},
  {path:'aquasan',component:AquasanComponent},
  {path:'azulev',component:AzulevComponent}
];

@NgModule({
  declarations: [
    AppComponent,
    ContactsComponent,
    HomeComponent,
    BrandsComponent,
    DownloadsComponent,
    CorporateComponent,
    NewseventsComponent,
    JobsComponent,
    QatarComponent,
    UaeComponent,
    KuwaitComponent,
    SaudiComponent,
    AquathermComponent,
    ValsirComponent,
    BravatComponent,
    AquasanComponent,
    AzulevComponent,
    LoginComponent,
    AboutComponent,
    VideosComponent,
    ProjectsComponent,
    GalleryComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    RouterModule.forRoot(appRoutes,{enableTracing: true}),
    FormsModule,
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyC2zSh3y7iENlPgdxL3P1I6DbhJSJSkNHI'
    })
  ],
  //put data provider here
  providers: [{provide:APP_BASE_HREF, useValue:'/'}],
  bootstrap: [AppComponent]
})
export class AppModule { }



